$primary_color: #f4645f;

.wedding {
	padding: 10px 0;
	/*background: #FFF5F5;*/
	background: #FFE6E6;

	.timeline-grid {
		padding-top: 30px;

		.row {
			padding: 0 !important;

			.column {

				&.with-content {
					padding-bottom: 40px;
				}

				&.left {
					padding-right: 0;

					&.with-content {
						padding-right: 70px;
					}

					.event-wrapper {
						&:after {
						    right: 0;
						    position: absolute;
						    z-index: 2;
						    content: '';
						    top: 29px;
						    background-color: #FFF;
						    width: 14px;
						    height: 14px;
						    -webkit-transition: background-color .1s ease,opacity .1s ease,color .1s ease,box-shadow .1s ease;
						    transition: background-color .1s ease,opacity .1s ease,color .1s ease,box-shadow .1s ease;
						    -webkit-transform: translateY(-50%) translateX(50%) rotate(-45deg);
						    transform: translateY(-50%) translateX(50%) rotate(-45deg);
						}
					}

					.event-time {
						text-align: right;
						right: -32px;

						.icon-box {
							.time {
								padding-right: 15px;
							}
						}
					}
					
				}

				&.right {
					border-left: 2px solid #FF9D9A;

					.event-wrapper {
						&:after {
						    left: -14px;
						    position: absolute;
						    z-index: 2;
						    content: '';
						    top: 29px;
						    background-color: #FFF;
						    width: 14px;
						    height: 14px;
						    -webkit-transition: background-color .1s ease,opacity .1s ease,color .1s ease,box-shadow .1s ease;
						    transition: background-color .1s ease,opacity .1s ease,color .1s ease,box-shadow .1s ease;
						    -webkit-transform: translateY(-50%) translateX(50%) rotate(-45deg);
						    transform: translateY(-50%) translateX(50%) rotate(-45deg);
						}
					}

					.event-time {
						left: -29px;

						.icon-box {
							.time {
								padding-left: 15px;
							}
						}
					}
				}

				.event-wrapper {
					padding: 20px;
					background: #ffffff;
					position: relative;

					.event-title, .ctime {
						color: #FF8A87;
						display: inline-block;
						margin-top: 0;
					}

					.ctime {
						display: none;
						visibility: hidden;
					}
				}

				.event-time {
					position: absolute;
					z-index: 10;

					.icon-box {
						.icon {
							&.coral {
								background-color: $primary_color !important;
								color: #ffffff;

								&.bordered {
									box-shadow: 0em 0em 0em 0.1em rgba(255, 255, 255, 1);
								}
							}
						}

						.time {
							color: $primary_color;
						}
					}
				}

			}
		}

	}
}

@media only screen and (min-width: 992px) {
	.wedding {
		.timeline-grid {
			.row {
				.column {
					.right {
						padding: 0;

						.with-content {
							padding-left: 70px;
						}
					}
				}
			}
		}
	}
}